.News {
    box-shadow: 1px 1px 2px 1px #888888;
    text-align: center;
    width: 80%;
    margin: 0 auto;
    height: 100%;
}

.dropdown {
    width: 300px;
    border: none;
    height: 30px;
}

li {
    list-style: none;
    padding: 10px 10px;
    display: inline;
}

li:active {
    background-color: lawngreen;
}