.casebox_green {
    border-radius: 4px;
    background-color: #8feac4;
    margin-top: 20px;
    text-align: center;
    height: 65px;
    width: 85px;
    padding: 10px 10px;
    display: inline-block;
    transform: scale(0, 0);
    animation: pulse 5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
}

